.card {
    margin: 20px 1px;
    background-color: white;
    border-radius: 10px;
    border: 2px solid rgb(229, 186, 193);

    & a button {
        height: 50px;
        width: 100px;
        border-radius: 10px;
        margin-bottom: 10px;
        cursor: pointer;

        &:hover {
            height: 55px;
            width: 105px;
            background-color: rgb(229, 186, 193);
            font-size: 20px;
        }
    }

    &__time {
        font-size: 20px;
        padding: 20px;
    }
}

.card-name {
    font-size: 25px;
    color: white;
    padding: 20px;
    margin: 1px;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
}

.card-info {
    font-size: 20px;
    background-color: rgb(239, 232, 232);
    padding: 20px;
}

span {
    font-size: 20px;
}

.blue-card .card-name {
    background-color: rgb(33, 156, 170);
}

.green-card .card-name {
    background-color: rgb(31, 136, 114);
}

.red-card .card-name {
    background-color: rgb(196, 85, 78);
}

.black-card .card-name {
    background-color: rgb(40, 46, 55);
}
