  h4 {
    text-align: center;
    font-size: calc((100vw + 2748px) / 261);
    border: none;
  }

  h3 {
    font-size: calc((100vw + 7680px) / 570);
  }

  span {
    font-family: "Annabelle", sans-serif;
  }

  .contact {
    bottom: 0;
    width: 100vw;
    // z-index: 100;
    font-size: 16px;
    color: rgb(91, 89, 89);
    background-image: url('../Body/fon2.jpg');
    background-size: cover;

    margin: auto;
    min-height: calc(100vh - 100px);



    &__item {
      width: 100vw;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap-reverse;
      justify-content: space-evenly;
      margin: auto;
      // background-image: url('../Body/fon3.jpg');
      // background-size: cover;
      min-height: calc(100vh - 50px);
      align-content: center;

      &::after {
        content: "";
        background-image: url('../Body/fon3.jpg');
        background-size: cover;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        opacity: 0.8;
        z-index: -1;
      }

    }

    &__adress {
      margin: 50px;
      display: flex;
      flex-direction: row;
      justify-content: center;
    }

    &__map {
      margin: 20px 0;
      // width: 40%;
    }

    &__contacts {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin: 20px 0;
      padding: 0 1em;
      width: 40%;

      &_vk {
        margin-left: -3px;
      }
    }

    & p {
      margin: 0;
      font-size: calc((100vw + 1600px) / 190);
    }
  }

  img {
    height: 30px;
  }

  .grid-container {
    display: grid;
    grid-template-columns: 0.2fr 2fr;
    grid-template-rows: 50px;
    align-items: center;
    row-gap: 5px;
  }
